import { Dispatch } from "redux";
import { ThunkAction } from "redux-thunk";
import { getObjects } from "../../DAL/Api";
import { ObjectType } from "../../Types/Types";
import { AppStateType } from "../store";

type ThunkType=ThunkAction<Promise<void>,AppStateType,unknown,ActionType>
const SET_OBJECTS:"headerReducer/SET_OBJECTS"="headerReducer/SET_OBJECTS"
const SET_CURRENT_OBJECT:"headerReducer/SET_CURRENT_OBJECT"="headerReducer/SET_CURRENT_OBJECT"

let initialState={
    objects:[] as ObjectType[],
    currentObject:null as ObjectType | null
}

type InitialStateType=typeof initialState
type ActionType=SetObjectsType | SetCurrentObjectType
 
export const headerReducer=(state=initialState,action:ActionType):InitialStateType=>{
    switch(action.type){
        case SET_OBJECTS:
            return{
                ...state,
                objects:[...action.objects]
            }
        case SET_CURRENT_OBJECT:
            return{
                ...state,
                currentObject:action.currentObject
            }
        default:
            return state
    }
}

type SetCurrentObjectType={
    type:typeof SET_CURRENT_OBJECT
    currentObject:ObjectType
}
export const setCurrentObject=(currentObject:ObjectType):SetCurrentObjectType=>{
    return{
        type:SET_CURRENT_OBJECT,
        currentObject
    }
}
type SetObjectsType={
    type:typeof SET_OBJECTS
    objects:ObjectType[]
}
const setObjects=(objects:ObjectType[]):SetObjectsType=>{
    return{
        type:SET_OBJECTS,
        objects
    }
}
export const setObjectsThunk=()=>async (dispatch:Dispatch)=>{
    let response = await getObjects()

    dispatch(setObjects(response.data))
}