import { Navbar,Nav } from "react-bootstrap";
import React, { useEffect, useState } from "react";
import { NavLink, useHistory } from "react-router-dom";
import { getObjects } from "../../DAL/Api";
import { setCurrentObject, setObjectsThunk } from "../../BLL/Reducers/headerReducer";
import { useDispatch,useSelector } from "react-redux";
import { currentObjectSelector, objectsSelector } from "../../BLL/Selectors/headerSelector";

type PropsType={}

export let Header:React.FC<PropsType>=((props)=>{
    
    let objects=useSelector(objectsSelector)
    let currentObject=useSelector(currentObjectSelector)
    const dispatch=useDispatch()
    let history=useHistory()

    let onClick=(e:any)=>{
        dispatch(setCurrentObject(objects.filter(o=>+(o.id)==e.target.id)[0]))
    }
    
    useEffect(()=>{
        if(objects.length===0){
            dispatch(setObjectsThunk())
        }else{
        let myURL=new URLSearchParams(history.location.search)
        if (myURL.get('object')){
            //@ts-ignore
            dispatch(setCurrentObject(objects.filter(o=>o.id==myURL.get('object'))[0]))
        }else{
            dispatch(setCurrentObject(objects[0]))
        }}
    },[objects])

    return<Navbar sticky="top" variant="dark" bg="success" expand="md" collapseOnSelect>
        <Navbar.Brand>
            <NavLink style={{color:'white',textDecoration:'none'}} 
            to={`/main?object=${currentObject?.id}`}>
                {currentObject?.title}
            </NavLink>
        </Navbar.Brand>
        <Navbar.Toggle aria-controls="responsive-navbar-nav"/>
        <Navbar.Collapse id="responsive-navbar-nav">
            <Nav className="mr-auto">
                {objects.map(o=>o.id!=currentObject?.id ? <Nav.Link className="center"><NavLink to={`/main?object=${o.id}`} className="NavbarLink">
                    <span onClick={onClick} id={String(o.id)}>{o.title}</span>
                </NavLink></Nav.Link> : "")}
            </Nav>
        </Navbar.Collapse>
    </Navbar>
})