import axios from "axios";
import { MaterialType, NumberType, ObjectType, QuestionType, VariantDetailType, VariantType } from "../Types/Types";


const instance=axios.create({
    withCredentials:true,
    baseURL:"http://a0535206.xsph.ru/api/",
    headers:{
        withCredentials:true
    }
})

export const getNumbers=(currentObject:number)=>{
    return instance.get<NumberType[]>(`numbers?item=${currentObject}`)
}
type GetQuestionsType={
    count:number
    next:null | string
    previous:null | string
    results:QuestionType[]
}
type GetVariantType={
    count:number
    next:null | string
    previous:null | string
    results:VariantType[]
}
export const getQuestions=(numberQuest?:number,categoryId=0,page=1,page_size=0,sortByDifficult=null as null | boolean)=>{
    return instance.get<GetQuestionsType>(`questions?${numberQuest && `number=${numberQuest}`}&category=${categoryId>0 ? categoryId : ""}&page=${page}&page_size=${page_size>0 ? page_size : ""}&sort_difficult=${sortByDifficult}`)
}
export const getRandomQuestions=(numberQuest?:number,categoryId=0 as number,page=1,page_size=0)=>{
    return instance.get<GetQuestionsType>(`questions?number=${numberQuest}&category=${categoryId>0 ? categoryId : ""}&page=${page}&page_size=${page_size>0 ? page_size : ""}&randomize=true`)
}
export const getQuestion=(id:number)=>{
    return instance.get<QuestionType>(`question?id=${id}`)
}
export const getVariants=(page:number=1,currentObject:number)=>{
    return instance.get<GetVariantType>(`variants?page=${page}&item=${currentObject}`)
}
export const getVariant=(id:number)=>{
    return instance.get<VariantDetailType>(`variant?id=${id}`)
}
export const getMaterial=(id:number)=>{
  return instance.get<MaterialType>(`material?id=${id}`)
}
export const getObjects=()=>{
    return instance.get<ObjectType[]>(`items`)
}
