import { NavLink } from "react-router-dom"
import { QuestionType } from "../../../Types/Types"
import React,{ useState } from "react";
import parse from "html-react-parser";

type OwnPropsType={}
type PropsType=OwnPropsType & QuestionType

export const TrainQuestion:React.FC<PropsType>=(props)=>{

    let [showAnswer,setShow]=useState(false)

    return<div  className="card mt-2">
        <div className="card-header">
            #<NavLink to={`solveQuestion?id=${props.id}`}>
                {props.id}
            </NavLink> -  {props.number.number}
            <div className="right">
                Сложность {props.difficult}/10
            </div>
        </div>
        <div className="card-body">
            <div>
                {props.introduction && parse(props.introduction)}
            </div>
            <div>
                {props.secondAsk && "a)"}{parse(props.firstAsk)}
            </div>
            <div>
                {props.secondAsk && `б)${parse(props.secondAsk)}`}
            </div>
            <div>
                {props.thirdAsk && `в)${parse(props.thirdAsk)}`}
            </div>
            <div>
                {props.imgForAsk && <div className="container center">
                <img className="img w-100 center" src={props.imgForAsk}/>
                </div>}
            </div>
            <div>
                {showAnswer && <div className="mt-2">
                    <h3 className="text-success">Решение </h3>
                    <div>
                        {props.secondInstuction && "a)"}{parse(props.fistInstruction)}
                    </div>
                    <div>
                        {props.secondInstuction && 'б)'+`${parse(props.secondInstuction)}`}
                    </div>
                    <div>
                        {props.thirdIntroduction && 'в)'+`${parse(props.thirdIntroduction)}`}
                    </div>
                    <div>
                        {props.imgForAns && <div className="center container">
                            <img className="img w-100 center" src={props.imgForAns} />
                        </div>}
                    </div>
                    <div>
                        <span style={{fontSize:"1.2em"}}>Ответ:</span> {props.secondAnwer && 'а)'}{parse(props.firstAnswer)}
                        {props.secondAnwer && `, б)${parse(props.secondAnwer)}`}
                        {props.thirdAnswer && `, в)${parse(props.thirdAnswer)}`}
                    </div>
                    {props.addition && <div className="mt-2 text-secondary">
                        <h4>Примечание</h4>
                        <div>
                            {parse(props.addition)}
                        </div>
                    </div>}
                </div>}
            </div>
        </div>
        <div className="card-footer">
            {props.source &&
            <div className="left pt-1">
            <a href={props.source_link} target={"_blank"}
            className="Link">
                {parse(props.source)}
            </a>
            </div>
            }
            <div className="right">
                <button onClick={()=>setShow(!showAnswer)}
                className="btn btn-light">
                    {
                    showAnswer ?
                    "Скрыть решение" :
                    "Показать решение"
                    }
                </button>
            </div>
        </div>
    </div>
}
