import { Formik } from "formik"
import React from "react"
import { useRef } from "react"
import { useDispatch } from "react-redux"
import { useHistory } from "react-router"
import { ChangeType } from "../../../BLL/Reducers/egeVariantReducer"
import { NumberType } from "../../../Types/Types"
import { Number } from "./NumberUI"


type PropsType={
    changeItems:NumberType[]
    submitForm:(filters:ChangeType[])=>void
}

export let FormChoiceWithFormik:React.FC<PropsType>=(props)=>{

    const history=useHistory()
    const dispatch=useDispatch()
    const toObj = (arr:Array<NumberType>) => arr.reduce((obj, el) => ({ ...obj, ...el }));

    let objCategorys={}
    let obj = {};
    props.changeItems.forEach((el) => {
        let key='number'+String(el.number)
        let object={} as any
        object[key]=0
        el.categorys.forEach(e=>{
            let newCategory={} as any
            let categoryKey='category'+String(e.id)+'number'+String(el.id)
            newCategory[categoryKey]=true
            Object.assign(objCategorys,newCategory)
        })
        Object.assign(obj,object);
    });

    return<Formik
    enableReinitialize
    initialValues={{...obj,...objCategorys}}
    onSubmit={(values)=>{
        let filters=[] as ChangeType[]
        props.changeItems.forEach(c=>{
            let myObj={
                count:0,
                number:c,
                categorys:[]
            } as ChangeType
            let key='number'+String(c.number)
            //@ts-ignore
            myObj.count=values[key]
            c.categorys.forEach(e=>{
                //@ts-ignore
                if(values['category'+String(e.id)+'number'+String(c.id)]){
                    myObj.categorys.push(...c.categorys.filter(f=>f.id==e.id))
                }
            })
            if(myObj.count>0 && myObj.count<=10){
                filters.push(myObj)
            }else if(myObj.count>10){
                myObj.count=10
                filters.push(myObj)
            }
        })
        history.push({
            pathname:'variant',
            search:'id=0'
        })
        dispatch(props.submitForm(filters))
    }}>
        {({
            handleSubmit,
            handleChange,
            values
        })=>{
            let myNumbers=props.changeItems.map(c=>
                <Number {...c} id={c.id} title={c.title} name={'number'+String(c.number)}
                categorys={c.categorys} number={c.number} points={c.points}
                benchmarks={c.benchmarks}
                //@ts-ignore
                value={values['number'+String(c.number)]}
                valuesCategory={c.categorys.map(cat=>{
                    return{name:('category'+String(cat.id)+'number'+String(c.id)),
                    //@ts-ignore
                    value:values['category'+String(cat.id)+'number'+String(c.id)]}})}
                handleChange={handleChange}/>)
            return<form onSubmit={handleSubmit}>
                {myNumbers}
                <div className="center mt-2">
                    <button className="btn btn-success"
                    type="submit" style={{fontSize:"1.1em"}}>
                        Начать тестирование
                    </button>
                </div>
            </form>
        }}
    </Formik>
}
