
const SET_RESULT:"egeResultReducer/SET_RESULT"="egeResultReducer/SET_RESULT"

export type QuestionTypeA={
    id:number
    rightAns:string
    anotherAns:string
    numberQuestion:number
}
export type QuestionTypeB={
    id:number
    points:number
    maxPoints:number
    numberQuestion:number
}
type QuestionsType={
    A:QuestionTypeA[]
    B:QuestionTypeB[]
}
const initialState={
    questions:{
        A:[] as QuestionTypeA[],
        B:[] as QuestionTypeB[]
    },
    result:0 as number,
    maxResult:0 as number
}

type InitialStateType=typeof initialState
type ActionType=SetResultType

export const egeResultReducer=(state=initialState,action:ActionType):InitialStateType=>{
    switch(action.type){
        case SET_RESULT:
            if(action.isToZero){
                return{
                    ...state,questions:{
                        A:[],
                        B:[]
                    },
                    result:0,maxResult:0
                }
            }
            let res=0
            let maxResult=0

            if(action.questions.A.length!==0){
                action.questions.A.forEach(a=>{
                    if(a.rightAns.split('!').length==1)
                    {
                        a.rightAns.trim().toLowerCase() == a.anotherAns.trim().toLowerCase() && res++;
                        maxResult++
                    }else{
                        a.rightAns.split('!').forEach((q,index)=>{
                            q==a.anotherAns[index] && res++;
                            maxResult++
                        })
                    }
                })
            }else if(action.questions.B.length!==0){
                action.questions.B.forEach(b=>{
                    res=res+b.points
                    maxResult=maxResult+b.maxPoints
                })
            }

            return{...state,
                questions:{
                    A:[...state.questions.A,
                    ...action.questions.A],
                    B:[...state.questions.B,
                    ...action.questions.B]
                },
                result:(+state.result)+(+res),
                maxResult:state.maxResult+maxResult
            }
        default:
            return state
    }
}

type SetResultType={
    type:typeof SET_RESULT
    questions:QuestionsType
    isToZero:boolean
}
export  const setResult=(questions:QuestionsType,isToZero:boolean=false):SetResultType=>{
    return{
        type:SET_RESULT,
        questions,isToZero
    }
}
