import { Field } from "formik"
import React, { useState } from "react"
import { ChangeEvent } from "react"
import { Button, ButtonGroup, OverlayTrigger, Tooltip } from "react-bootstrap"
import { NavLink } from "react-router-dom"
import { NumberType } from "../../../Types/Types"
import parse from 'html-react-parser'


type valueCategoryType = {
    name: string
    value: boolean
}
type PropsType = {
    name: string
    value: number
    handleChange: (e: string | ChangeEvent<any>) => void
    valuesCategory: valueCategoryType[]
}
type PropsQuestionType = NumberType & PropsType
export const Number: React.FC<PropsQuestionType> = (props) => {
    const style = {
        borderBottom: "silver 1px solid",
        borderRadius: "10px",
        paddingBottom: "0em",
        marginTop: "1em",
        paddingLeft: "0.7em"
    }
    let [editMode, setEditMode] = useState(false) //false - hide , true - show detail
    return <div style={style}>
        <div className="row">
            <div className="col-7 pr-4 pt-2">

                <h5 className="d-flex">
                    <div className="mx-1 mb-1">
                        <Button variant="light" size="sm" onClick={() => setEditMode(!editMode)}>
                            <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" className="bi bi-list" viewBox="0 0 16 16">
                                <path fill-rule="evenodd" d="M2.5 12a.5.5 0 0 1 .5-.5h10a.5.5 0 0 1 0 1H3a.5.5 0 0 1-.5-.5zm0-4a.5.5 0 0 1 .5-.5h10a.5.5 0 0 1 0 1H3a.5.5 0 0 1-.5-.5zm0-4a.5.5 0 0 1 .5-.5h10a.5.5 0 0 1 0 1H3a.5.5 0 0 1-.5-.5z" />
                            </svg>
                        </Button>
                    </div>
                    <div className="center">
                    {props.number}.{parse(props.title)}
                    </div>
                </h5>
            </div>
            <div className="col-5 end yCenter">
                <div className="btn-group">
                    <button disabled={props.value <= 0}
                        style={{ maxHeight: "2.5em" }}
                        className="btn btn-success"
                        name={props.name} value={props.value}
                        onClick={(e) => {
                            //@ts-ignore
                            e.target.value = +(props.value) || props.value == 0 ? +(props.value) - 1 : 0
                            props.handleChange(e)
                        }} type="reset"
                    >-</button>

                    <input
                        style={{ width: "2.5em", maxHeight: "2.5em", backgroundColor: "#F8F9FA", border: 'none', outline: 'none', textAlign: 'center' }}
                        value={props.value}
                        name={props.name}
                        onChange={(e) => {
                            e.target.value = (+(e.target.value) || e.target.value == '0') ? e.target.value : '0'
                            if (+(e.target.value) > 10) {
                                e.target.value = '10'
                            }
                            if (e.target.value.length > 1 && e.target.value[0] == "0") {
                                e.target.value = e.target.value.slice(1,)
                            }
                            props.handleChange(e)
                        }} />

                    <button className="btn btn-success"
                        style={{ maxHeight: "2.5em" }}
                        disabled={props.value >= 10}
                        name={props.name}
                        onClick={(e) => {
                            //@ts-ignore
                            e.target.value = +(props.value) || props.value == 0 ? +(props.value) + 1 : 0
                            props.handleChange(e)
                        }} type="reset"
                        value={props.value}>
                        +
                    </button>
                </div>
            </div>
        </div>
        {editMode &&
            <div className="my-1">
                {props.categorys.map((c, index) => <div className="mt-1 row">
                    <div className="col-10 d-flex">
                        <div>
                        {c.material ?
                        <OverlayTrigger
                        key={"bottom-have"}
                        placement={"bottom"}
                        overlay={
                          <Tooltip id={`tooltip-${"yes"}`}>
                            К этому заданию есть материалы.
                          </Tooltip>
                        }
                      >
                        <Button variant="light">
                        <NavLink to={`material?id=${c.material.id}`} className="">
                          <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" className="bi bi-info-circle" viewBox="0 0 16 16">
                            <path d="M8 15A7 7 0 1 1 8 1a7 7 0 0 1 0 14zm0 1A8 8 0 1 0 8 0a8 8 0 0 0 0 16z"/>
                            <path d="m8.93 6.588-2.29.287-.082.38.45.083c.294.07.352.176.288.469l-.738 3.468c-.194.897.105 1.319.808 1.319.545 0 1.178-.252 1.465-.598l.088-.416c-.2.176-.492.246-.686.246-.275 0-.375-.193-.304-.533L8.93 6.588zM9 4.5a1 1 0 1 1-2 0 1 1 0 0 1 2 0z"/>
                          </svg>
                        </NavLink> 
                        </Button>
                      </OverlayTrigger>
                        :
                        
                        <OverlayTrigger
                        key={"bottom-not-have"}
                        placement={"bottom"}
                        overlay={
                          <Tooltip id={`tooltip-${"not"}`}>
                            К этому заданию еще нет материалов.
                          </Tooltip>
                        }
                      >
                        <Button variant="light">
                          <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" className="bi bi-info-circle" viewBox="0 0 16 16">
                            <path d="M8 15A7 7 0 1 1 8 1a7 7 0 0 1 0 14zm0 1A8 8 0 1 0 8 0a8 8 0 0 0 0 16z"/>
                            <path d="m8.93 6.588-2.29.287-.082.38.45.083c.294.07.352.176.288.469l-.738 3.468c-.194.897.105 1.319.808 1.319.545 0 1.178-.252 1.465-.598l.088-.416c-.2.176-.492.246-.686.246-.275 0-.375-.193-.304-.533L8.93 6.588zM9 4.5a1 1 0 1 1-2 0 1 1 0 0 1 2 0z"/>
                          </svg> 
                        </Button>
                      </OverlayTrigger>}
                        </div>
                        <div className="center">
                            <label htmlFor={`${c.id}`}>{parse(c.title)}</label> - <NavLink to={`train?id=${c.id}&page=1`}>Посмотреть все</NavLink>
                        </div>
                    </div>
                    <div className="col-2 center"> <Field
                        type="checkbox"
                        name={props.valuesCategory[index].name}
                        onChange={props.handleChange}
                        defaultChecked={props.valuesCategory[index].value}
                        id={`${c.id}`}
                    /></div>
                </div>)}
            </div>}
    </div>
}
