import { QuestionType } from "../../Types/Types";


const SET_RESULT:"preResultReducer/SET_RESULT"="preResultReducer/SET_RESULT"

const initialState={
    questions:[] as QuestionType[]
}

type InitialStateType=typeof initialState
type ActionType=SetResultType

export const preResultReducer=(state=initialState,action:ActionType):InitialStateType=>{
    switch(action.type){
        case SET_RESULT:
            return{...state,questions:[...action.questions]}
        default:
            return state
    }
}

type SetResultType={
    type:typeof SET_RESULT
    questions:QuestionType[]
}
export  const setPreResult=(questions:QuestionType[]):SetResultType=>{
    return{
        type:SET_RESULT,
        questions
    }
}