import { useEffect, useState } from "react"
import { useDispatch, useSelector } from "react-redux"
import { useHistory } from "react-router"
import { setQuestion, setQuestionThunk } from "../../../BLL/Reducers/singleQuestionReducer"
import { singleQuestionSelector } from "../../../BLL/Selectors/singleQuestionSelector"
import { BenchmarkType, CategoryType, NumberType, QuestionType } from "../../../Types/Types"
import parse from 'html-react-parser'


type OwnPropsType={}
type PropsType=OwnPropsType
export const SingleQuestion:React.FC<PropsType>=(nativeProps)=>{

    let props=useSelector(singleQuestionSelector)
    let history=useHistory()
    const dispatch=useDispatch()

    useEffect(()=>{

        const myURL=new URLSearchParams(history.location.search)

        //@ts-ignore
        let id=+myURL.get('id') //id string - parse to int
        dispatch(setQuestionThunk(id))

        //for zero question
        return(()=>{
            dispatch(setQuestion(zeroQuestion))
        })
    },[history.location.search])

    let [showAnswer,setShow]=useState(false)

    return<div  className="mt-2">
        <div className="">
        №{props.number.number} - #{props.id}
            <div className="right">
                Сложность {props.difficult}/10
            </div>
        </div>
        <div className="">
            <div>
                {props.introduction && parse(props.introduction)}
            </div>
            <div>
                {props.secondAsk && "a)"}{parse(props.firstAsk)}
            </div>
            <div>
                {props.secondAsk && `б)${parse(props.secondAsk)}`}
            </div>
            <div>
                {props.thirdAsk && `в)${parse(props.thirdAsk)}`}
            </div>
            <div>
                {props.imgForAsk && <div className="center">
                <img className="img" src={props.imgForAsk}/>
                </div>}
            </div>
            <div>
                {showAnswer && <div className="mt-2">
                    <h3 className="text-success">Решение </h3>
                    <div>
                        {props.secondInstuction && "a)"}{parse(props.fistInstruction)}
                    </div>
                    <div>
                        {props.secondInstuction && 'б)'+`${parse(props.secondInstuction)}`}
                    </div>
                    <div>
                        {props.thirdIntroduction && 'в)'+`${parse(props.thirdIntroduction)}`}
                    </div>
                    <div>
                        {props.imgForAns && <div className="center">
                            <img className="img" src={props.imgForAns} />
                        </div>}
                    </div>
                    <div>
                        <span style={{fontSize:"1.2em"}}>Ответ:</span> {props.secondAnwer && 'а)'}{parse(props.firstAnswer)}
                        {props.secondAnwer && `, б)${parse(props.secondAnwer)}`}
                        {props.thirdAnswer && `, в)${parse(props.thirdAnswer)}`}
                    </div>
                    {props.addition && <div className="mt-2">
                        <h4>Примечание</h4>
                        <div>
                            {parse(props.addition)}
                        </div>
                    </div>}
                </div>}
            </div>
        </div>
        <div className="end">
            {props.source &&
            <div className="left pt-1">
            <a href={props.source_link} target={"_blank"}
            className="Link">
                {parse(props.source)}
            </a>
            </div>
            }
            <div className="right">
                <button onClick={()=>setShow(!showAnswer)}
                className="btn btn-light">
                    {
                    showAnswer ?
                    "Скрыть решение" :
                    "Показать решение"
                    }
                </button>
            </div>
        </div>
    </div>
}






let zeroQuestion={
    id:0 as number,
    number:{
        id:0,
        categorys:[] as CategoryType[],
        title:"" ,
        number:0,
        points:0,
        benchmarks:[] as BenchmarkType[]
    } as NumberType,
    categorys:{
        id:0,
        title:""
    } as CategoryType,
    difficult:0 as number,
    introduction:"" as string,
    firstAsk:"" as string,
    secondAsk:"" as string,
    thirdAsk:"" as string,
    imgForAsk:"" as string,

    imgForAns:"" as string,
    fistInstruction:"" as string,
    secondInstuction:"" as string,
    thirdIntroduction:"" as string,

    firstAnswer:"" as string,
    secondAnwer:"" as string,
    thirdAnswer:"" as string,

    addition:"" as string,
    source:"" as string,
    source_link:"" as string
}
