import { NavLink } from "react-router-dom"
import { QuestionTypeA, QuestionTypeB } from "../../../BLL/Reducers/egeResultReducer"



type PropsType={
    id:number
    rightAns?:string
    anotherAns?:string
    numberQuestion:number
    points?:number
    maxPoints?:number
}
export const BarResult:React.FC<PropsType>=(props)=>{
    const checkAnswer=(anotherAns:string,rightAns:string):string=>{
        if(!anotherAns.trim()){
            return ""
        }
        if(rightAns.split('!').length>1){
            let countRightAns=0
            rightAns.split('!').forEach((r,index)=>{
                if(r===anotherAns[index]){
                    countRightAns++
                }
            })
            if(countRightAns===rightAns.split('!').length){
                return "mySuccess"
            }else if(countRightAns>0){
                return "myWarning"
            }else{
                return "myDanger"
            }
        }else if(anotherAns===rightAns){
            return "mySuccess"
        }else{
            return "myDanger"
        }
    }
    if(props.maxPoints){
        return<tr>
            <td className="center">
                {props.numberQuestion}
            </td>
            <td className="center">
                <NavLink to={`solveQuestion?id=${props.id}`}>
                    {props.id}
                </NavLink>
            </td>
            <td className={props.maxPoints==props.points ?
             "mySuccess center text-white" :
              props.points && props.points>0 ?
              "myWarning center text-white" :
              "myDanger center text-white"} >
                {props.points}
            </td>
            <td className="center">
                {props.maxPoints}
            </td>
        </tr>
    }
    return<tr>
        <td className="center">
            {props.numberQuestion}
        </td>
        <td className="center">
            <NavLink to={`solveQuestion?id=${props.id}`}>
                {props.id}
            </NavLink>
        </td>
        <td className={"center text-white "+checkAnswer(
            props.anotherAns ?
            props.anotherAns.toLowerCase() : "" ,
            props.rightAns ?
            props.rightAns.toLowerCase() : "")}>
            {props.anotherAns?.toLowerCase()}
        </td>
        <td className="center">
            {props.rightAns && props.rightAns.split("!").length>1 ?
                props.rightAns?.split("!").map(r=>r) :
                props.rightAns?.toLowerCase()
            }
        </td>
    </tr>
}
