import { BenchmarkType } from "../../../Types/Types"
import parse from "html-react-parser";

type OwnPropsType={
    index:number
}
type PropsType=OwnPropsType & BenchmarkType

export const BenchmarkUI:React.FC<PropsType>=(props)=>{
    return<tr>
        <td className="center">
            {props.index}
        </td>
        <td className="center">
            {parse(props.title)}
        </td>
        <td className="center">
            {props.points}
        </td>
    </tr>
}