import React,{useEffect, useState} from "react"
import { ButtonGroup,Button } from "react-bootstrap"
import { useDispatch, useSelector } from "react-redux"
import { useHistory } from "react-router"
import { setNumbersThunk, setVariantsThunk } from "../../../BLL/Reducers/egeChoiceReducer"
import { totalCountSelector,variantsSelector,numbersSelector } from "../../../BLL/Selectors/egeChoiceSelector"
import { NumberType, VariantType } from "../../../Types/Types"
import { Pagination } from "../../Bricks/Pagination"
import { Number } from "./NumberUI";
import { FormChoiceWithFormik } from "./FormChoice"
import {setQuestionThunk} from '../../../BLL/Reducers/egeVariantReducer'
import { currentObjectSelector } from "../../../BLL/Selectors/headerSelector"

type PropsType = {}

export const EgeChoice: React.FC<PropsType> = (props) => {
    
    const dispatch=useDispatch()
    const totalCount=useSelector(totalCountSelector)
    const variants=useSelector(variantsSelector)
    const numbers=useSelector(numbersSelector)
    const currentObject=useSelector(currentObjectSelector)

    useEffect(()=>{
        dispatch(setVariantsThunk(1,currentObject ? currentObject.id : 1))
        dispatch(setNumbersThunk(currentObject ? currentObject.id : 1))
    },[currentObject])

    return <div>
        <div className="row">
            <div className="col-md-6 text-center">
                <h2>Наши варианты</h2>
            </div>
            <div className="col-md-6 center">
                <div className="">
                    <Pagination totalCount={totalCount} behavior={false}
                    pS={5} page={totalCount} link={"/variant"}/>
                </div>
            </div>
        </div>
        <div>
            <h2>Составить собственный вариант</h2>
            <div>
                <FormChoiceWithFormik changeItems={numbers} submitForm={setQuestionThunk}/>
            </div>
        </div>
    </div>
}

let v:VariantType[]=[]
for(let i=103;i>0;i--){
    v.push({
            id: i,
            variantNumber: i,
            questions: []
        })
}

