import { Formik } from "formik"
import React from "react"
import { useDispatch, useSelector } from "react-redux"
import { useHistory } from "react-router"
import { QuestionTypeA, setResult } from "../../../BLL/Reducers/egeResultReducer"
import { setPreResult } from "../../../BLL/Reducers/preResultReducer"
import { questionsVariantSelector } from "../../../BLL/Selectors/egeVariantSelector"
import { AppStateType } from "../../../BLL/store"
import { QuestionType } from "../../../Types/Types"
import { VariantQuestion } from "./VariantQuestion"


type PropsType={
    questionsRaw:QuestionType[]
}

export const FormVariant:React.FC<PropsType>=(props)=>{
   
    let questionValues={}
    props.questionsRaw.forEach(q=>{
        let newQ:any={}
        newQ['question'+String(q.id)]=""
        Object.assign(questionValues,newQ)
    })

    const dispatch=useDispatch()
    let history=useHistory()

    return<Formik
    enableReinitialize
    initialValues={{...questionValues}}
    onSubmit={(values)=>{
        
        let questionsA:QuestionTypeA[]=[]
        let questionsB:QuestionType[]=[]
        props.questionsRaw.forEach(q=>{
            if(q.number.points==1 || q.firstAnswer.split('!').length>1){
            questionsA.push({
                id:q.id,
                rightAns:q.firstAnswer,
                numberQuestion:q.number.number,
                //@ts-ignore
                anotherAns:values[`question${q.id}`]
            })}else{
                questionsB.push(q)
            }
        })

        dispatch(setPreResult(questionsB))
        dispatch(setResult({
            A:[...questionsA],
            B:[]
        }))
        if(questionsB.length>0){
            history.push({
                pathname:'preResult'
            })
        }else{
            history.push({
                pathname:'result'
            })
        }
    }}>
        {({
            handleSubmit,
            values,
            handleChange
        })=>{
            let questionUIVariant:Array<any>=props.questionsRaw.map(q=>{return <VariantQuestion 
                //@ts-ignore
                {...q} name={`question${q.id}`} value={values[`question${q.id}`]}
                onChange={handleChange} />})
            return<form onSubmit={handleSubmit}>
                {questionUIVariant}
                <div className="center mt-1">
                    <button type="submit" 
                    className="btn btn-success">
                        <span style={{fontSize:"1.2em"}}>
                            Закончить работу
                        </span>
                    </button>
                </div>
            </form>
        }}
    </Formik>
}