import { AppStateType } from "../store";


export const questionsVariantSelector=(state:AppStateType)=>{
    return state.variantPage.questions
}
export const numberVariantSelector=(state:AppStateType)=>{
    return state.variantPage.numberVariant
}
export const modeSelector=(state:AppStateType)=>{
    return state.variantPage.mode
}
export const pageVariantSelector=(state:AppStateType)=>{
    return state.variantPage.page
}
export const sortSelector=(state:AppStateType)=>{
    return state.variantPage.sort
}
export const totalCount=(state:AppStateType)=>{
    return state.variantPage.totalCount
}