import { Formik } from "formik"
import React, { useEffect, useState } from "react"
import { useDispatch, useSelector } from "react-redux"
import { useHistory } from "react-router"
import { JsxElement } from "typescript"
import { QuestionTypeA, setResult } from "../../../BLL/Reducers/egeResultReducer"
import { setTrainQuestionThunk, setVariantThunk } from "../../../BLL/Reducers/egeVariantReducer"
import { setPreResult } from "../../../BLL/Reducers/preResultReducer"
import { resultSelector } from "../../../BLL/Selectors/egeResultSelector"
import { pageVariantSelector,modeSelector, questionsVariantSelector, totalCount } from "../../../BLL/Selectors/egeVariantSelector"
import { NumberType, QuestionType } from "../../../Types/Types"
import { Pagination } from "../../Bricks/Pagination"
import { FormVariant } from "./FormVariant"
import { TrainQuestion } from "./TrainQuestion"
import { VariantQuestion } from "./VariantQuestion"




type PropsType={}

export const EgeVariantPage:React.FC<PropsType>=(props)=>{
    //null - first new,false - first hard , true - first easy
    let [sort,setSort]=useState<string>("null")
    
    let dispatch=useDispatch()
    let history=useHistory()
    let questionsRaw=useSelector(questionsVariantSelector)
    let result=useSelector(resultSelector)
    useEffect(()=>{
        let myURL=new URLSearchParams(history.location.search)
        switch(history.location.pathname){
            case "/train":
                //@ts-ignore
                let category=myURL.get('id') ? +myURL.get('id') && +myURL.get('id') : 1
                //@ts-ignore
                let page=myURL.get('page') ? +myURL.get('page') && +myURL.get('page') : 1
                
                //parse string into boolean | null
                let resultSort:boolean|null=null
                switch(sort){
                    case 'null':resultSort=null;break
                    case 'false':resultSort=false;break
                    case 'true':resultSort=true;break
                }
                //@ts-ignore
                dispatch(setTrainQuestionThunk(category,page,resultSort))
                break
            case "/variant":
                //@ts-ignore
                let idVariant=myURL.get('id') ? +myURL.get('id') && +myURL.get('id') : 0
                if(idVariant!=0){
                    dispatch(setVariantThunk(idVariant))
                }
                break
        }
    },[history.location.search,sort])

    let mode=useSelector(modeSelector)
    let myTotalCount=useSelector(totalCount)
    let page=useSelector(pageVariantSelector)

    let questionUITest:Array<any>=questionsRaw.map(q=>{return <TrainQuestion {...q}/>})
    

    switch(mode){
        case "train":
        return<div>
            <div className="row">
            <div className="col-md-4 center">
                <Pagination 
                page={page} totalCount={Math.ceil(myTotalCount/5)} pS={5} 
                behavior={true} link={"train"}/>
            </div>
            <div className="col-md-4 mt-2"></div>
            <div className="col-md-4 center">
                <select onChange={(e)=>setSort(e.target.value)}
                className="form-control">
                    <option value={"null"}>
                        Сначала новые
                    </option>
                    <option  value={"true"}>
                        Сначала легкие
                    </option>
                    <option  value={"false"}>
                        Сначала сложные
                    </option>
                </select>
            </div>
            </div>
            {questionUITest}
        </div>
        case "variant":
            return<FormVariant questionsRaw={questionsRaw} />
        default:
            return<div>
                Some error occured...
            </div>
    }
}