import { ChangeEvent } from "react"
import { useSelector } from "react-redux"
import { numbersSelector } from "../../../BLL/Selectors/egeChoiceSelector"
import { QuestionType } from "../../../Types/Types"
import parse from "html-react-parser";


type AddPropsType={
    name:string
    value:string
    onChange:(e: ChangeEvent<any>)=>void
}
type PropsType=QuestionType & AddPropsType

export const VariantQuestion:React.FC<PropsType>=(props)=>{

    let allNumbers=useSelector(numbersSelector)

    return<div className="card mt-2">
        <div className="card-header">
            №{props.number.number}
        </div>
        <div className="card-body">
            <div>
            {parse(props.introduction)}
            </div>
            <div>
                {props.secondAsk && "a)"}
                {props.firstAsk && parse(props.firstAsk)}
            </div>
            <div>
                {props.secondAsk && "б)"+parse(props.secondAsk)}
            </div>
            <div>
                {props.thirdAsk && "в)"+parse(props.thirdAsk)}
            </div>
            <div>
                {props.imgForAsk && <div className="center container">
                    <img className="img w-100 center" src={props.imgForAsk} />
                </div>}
            </div>
            {(props.number.points===1 || props.firstAnswer.split('!').length>1) ?
            <div className="mt-1">
                <input
                placeholder={'Ответ'}
                className={"form-control"}
                name={props.name}
                value={props.value}
                onChange={props.onChange}
                />
            </div>:
            <div className="text-secondary center mt-1">
                Задания с развернутым ответом
                будут представлены вам для самопроверки
                на следующей странице
            </div>}
        </div>
    </div>
}
