import React, { useState } from "react"
import { Button, ButtonGroup } from "react-bootstrap"
import { useHistory } from "react-router-dom"
import { VariantType } from "../../Types/Types"


type PropsType={
    totalCount:number
    page:number
    pS:number//portion Size
    link?:string
    behavior:boolean //true - first litle, false - first biggest 
}
export const Pagination:React.FC<PropsType>=({page,pS,totalCount,...props})=>{

    let history=useHistory()
    let onSubmit=(e:any)=>{
        if(props.link=="train"){
            const urlParams = new URLSearchParams(history.location.search);
            let id=(urlParams.get('id'))
            history.push({
                pathname:"train",
                search:`id=${id}&page=${e.target.innerText}`
            })
        }else{
        history.push({
            pathname:props.link,
            search:`?id=${e.target.innerText}`
        })}
    }

    let [pN,setPn]=useState(1)//pageNumber

    let onNext=()=>{
        setPn((pN)=>(pN+1))
    }
    let onPrev=()=>{
        setPn((pN)=>(pN-1))
    }

    let buttons=[] as any
    if(!props.behavior){
        for(let i=0;i<pS;i++){
            buttons.push(totalCount>(pN-1)*pS+i &&
                <Button variant="light" onClick={onSubmit} active={page==totalCount-((pN-1)*pS)-i}>
                    {totalCount-((pN-1)*pS)-i}
                </Button>)
        }
    }else{
        for(let i=1;i<=pS;i++){
            buttons.push(totalCount>=(pN-1)*pS+i &&
                <Button variant="light" onClick={onSubmit} active={page==((pN-1)*pS)+i}>
                    {((pN-1)*pS)+i}
                </Button>)
        }
    }


    return<div>
        <ButtonGroup size={"lg"} aria-label="Basic example">
            {pN>1 && 
            <Button variant="success" onClick={onPrev}>{"<"}</Button>}

            {buttons}

            {totalCount-(pN)*pS>0 &&
            <Button variant="success" onClick={onNext}>{">"}</Button>}
        
        </ButtonGroup>
    </div>
}