


import { setMaterialThunk } from "../../../BLL/Reducers/materialReducer";
import { useEffect } from "react";
import { useDispatch,useSelector } from "react-redux";
import { useHistory } from "react-router-dom";
import { materialSelector } from "../../../BLL/Selectors/materialSelector";
import parse from "html-react-parser";


type PropsType={}
export const Material:React.FC<PropsType>=(props)=>{

  let history=useHistory()
  const dispatch=useDispatch()
  let material=useSelector(materialSelector)

  useEffect(()=>{
      let myURL=new URLSearchParams(history.location.search)
      //@ts-ignore
      let id=+(myURL.get('id'))

      dispatch(setMaterialThunk(id))
  },[history.location.search])

  return<div>
    <h4>{parse(material.title)}</h4>
    <div>
      {parse(material.text)}
    </div>
    <div className="center">
      <img className="img w-100 round"
      src={material.imgMaterial}/>
    </div>
  </div>
}
