import { Formik } from "formik"
import { useDispatch } from "react-redux"
import { useHistory } from "react-router"
import { QuestionTypeA, QuestionTypeB, setResult } from "../../../BLL/Reducers/egeResultReducer"
import { BenchmarkType, QuestionType } from "../../../Types/Types"
import { QuestionUIB } from "./QuestionUIB"




type PropsType={
    questions:QuestionType[]
}
export const PreResultForm:React.FC<PropsType>=(props)=>{
    
    const dispatch=useDispatch()
    const history=useHistory()
    //Create values
    const myValues={}
    props.questions.forEach(q=>{
        let newObj={}
        //@ts-ignorets
        newObj[`question${q.id}`]=0
        Object.assign(myValues,newObj)
    })


    return<Formik
        initialValues={{...myValues}}
        onSubmit={(values)=>{
            let questions={
                A:[],
                B:[] as QuestionTypeB[]
            }
            
            props.questions.forEach(q=>{
              questions.B.push({
                  id:q.id,
                  maxPoints:q.number.points,
                  numberQuestion:q.number.number,
                  //@ts-ignore
                  points:values[`question${q.id}`]
              })  
            })
            dispatch(setResult(questions))
            history.push({
                pathname:'result'
            })
        }}>
    {({
        handleSubmit,
        handleChange,
        values
    })=>{
        const Benchmarks=props.questions.map(q=><QuestionUIB 
        {...q} name={`question${q.id}`} 
        //@ts-ignore
        value={values[`question${q.id}`]}
        onChange={handleChange}/>)
        
        return<form onSubmit={handleSubmit}>
            {Benchmarks}
            <div className="center">
            <button className="btn btn-success mt-1" 
            style={{fontSize:'1.2em'}} type="submit">
                Закончить проверку
            </button>
            </div>
        </form>
    }}
    </Formik>
}