import { Dispatch } from "redux";
import { getQuestions, getRandomQuestions, getVariant } from "../../DAL/Api";
import { CategoryType, NumberType, QuestionType } from "../../Types/Types";

const SET_VARIANT:"egeVariantReducer/SET_VARIANT"="egeVariantReducer/SET_VARIANT"

let curNumber:NumberType={
    title:"Par" ,
    id:1,
    categorys:[],
    number:1,
    points:1,
    benchmarks:[]
} 

type ModeType="train" | "variant"
const initialState={
    questions:[] as QuestionType[],
    numberVariant:null as number | null,
    mode:"variant" as ModeType,
    page:1 as number,
    //null - first new, true - first easy , false - first hard
    sort:null as null | boolean,
    totalCount:0 as number
}

type InitialStateType=typeof initialState
type ActionType=SetPageType

export const egeVariantReducer=(state=initialState,action:ActionType):InitialStateType=>{
    switch(action.type){
        case SET_VARIANT:
            return{...state,
            questions:[...action.questions],
            numberVariant:action.numberVariant,
            mode:action.mode,
            page:action.page,
            sort:action.sort,
            totalCount:action.totalCount
        }
        default:
            return state
    }
}

type SetPageType={
    type:typeof SET_VARIANT
    questions:QuestionType[]
    numberVariant:number | null
    mode:ModeType
    page:number
    sort:null | boolean
    totalCount:number
}
const setVariant=(questions:QuestionType[],numberVariant:number | null,mode:ModeType,page=1,sort=null as null|boolean,totalCount=0):SetPageType=>{
    return{
        type:SET_VARIANT,
        questions,numberVariant,
        mode,page,sort,
        totalCount
    }
}
export type ChangeType={
    number:NumberType
    categorys:CategoryType[]
    count:number
}
export const setTrainQuestionThunk=(category:number,page=1,sortByDifficult=null as boolean | null)=>async (dispatch:Dispatch)=>{
    let res = await getQuestions(undefined,category,page,5,sortByDifficult)

    dispatch(setVariant(res.data.results,null,"train",page,sortByDifficult,res.data.count))
}
export const setQuestionThunk=(filters:ChangeType[])=>async (dispatch:Dispatch)=>{
    let result:QuestionType[]=[]
    for(let c=0;c<filters.length;c++){
        let ans:QuestionType[]=[];
        if  (filters[c].number.categorys.length===filters[c].categorys.length){
            ans = (await getRandomQuestions(filters[c].number.number,0,1,filters[c].count)).data.results
        }
        else{
            let tryed=Math.floor(filters[c].count/filters[c].categorys.length)
            let ost=filters[c].count%filters[c].categorys.length
            for(let i=0;i<filters[c].categorys.length;i++){
                let a=(await getRandomQuestions(filters[c].number.number,filters[c].categorys[i].id,1,tryed)).data.results
                ans=[...ans,...a]
            }
            if(ost!==0){
                let r=Math.round(Math.random()*(filters[c].categorys.length-1))
                let a=(await getRandomQuestions(filters[c].number.number,filters[c].categorys[r].id,1,ost)).data.results
                ans=[...ans,...a]
            }
        }
        result=[...result,...ans]
    }
    dispatch(setVariant(result,null,"variant"))
}
export const setVariantThunk=(numberVariant:number)=>async (dispatch:Dispatch)=>{
    let response = await getVariant(numberVariant)

    dispatch(setVariant(response.data.questions,numberVariant,"variant"))
}
