import { ChangeEvent } from "react"
import { NavLink } from "react-router-dom"
import { QuestionType } from "../../../Types/Types"
import { BenchmarkUI } from "./BenchmarkUI"
import parse from "html-react-parser";



type OwnPropsType = {
    value:number
    name:string
    onChange:(e: string | ChangeEvent<any>)=>void
}
type PropsType = QuestionType & OwnPropsType
export const QuestionUIB: React.FC<PropsType> = (props) => {
    //Fill options for form
    let options=[]
    for(let i=0;i<=props.number.points;i++){
        options.push(<option value={i}>{i}</option>)
    }
    //Fill benchmarks
    let benchmarks=[]
    benchmarks=props.number.benchmarks.map((b,index)=><BenchmarkUI
        index={index+1} {...b} />)

    return <div className="mt-2">
        <div className="card">
            <div className="card-header">
                #<NavLink to={`solveQuestion?id=${props.id}`}>{props.id}</NavLink> - №{props.number.number}
            </div>
            <div className="card-body">
                <div>
                    {props.introduction && parse(props.introduction)}
                </div>
                <div>
                    {props.secondAsk && "a)"}{parse(props.firstAsk)}
                </div>
                <div>
                    {props.secondAsk && `б)${parse(props.secondAsk)}`}
                </div>
                <div>
                    {props.thirdAsk && `в)${parse(props.thirdAsk)}`}
                </div>
                <div>
                    {props.imgForAsk && <div className="center container">
                        <img className="img w-100 center" src={props.imgForAsk} />
                    </div>}
                </div>
                <div>
                    <div className="mt-2">
                        <h4 className="text-success">Решение </h4>
                        <div>
                            {props.secondInstuction && "a)"}{parse(props.fistInstruction)}
                        </div>
                        <div>
                            {props.secondInstuction && 'б)' + `${parse(props.secondInstuction)}`}
                        </div>
                        <div>
                            {props.thirdIntroduction && 'в)' + `${parse(props.thirdIntroduction)}`}
                        </div>
                        <div>
                            {props.imgForAns && <div className="center container">
                                <img className="img w-100 center" src={props.imgForAns} />
                            </div>}
                        </div>
                        <div>
                            <span style={{ fontSize: "1.2em" }}>Ответ:</span> {props.secondAnwer && 'а)'}{parse(props.firstAnswer)}
                            {props.secondAnwer && `, б)${parse(props.secondAnwer)}`}
                            {props.thirdAnswer && `, в)${parse(props.thirdAnswer)}`}
                        </div>
                        {props.addition && <div className="mt-2">
                            <h4>Примечание</h4>
                            <div>
                                {parse(props.addition)}
                            </div>
                        </div>}

                        <div className="mt-1">
                            <h5>Критерии оценивания</h5>
                            <table className="table border-success table-hovered">
                                <thead>
                                    <tr >
                                        <th className="center yCenter">№</th>
                                        <th className="center yCenter">Условие</th>
                                        <th className="center yCenter">
                                            Макс. кол-во баллов
                                        </th>
                                    </tr>
                                </thead>
                                <tbody>
                                    {benchmarks}
                                </tbody>
                            </table>
                        </div>
                    </div>
                </div>
                <div className="mt-1 d-flex">
                    Колличество баллов:
                    <select onChange={props.onChange}
                    className="form-control"
                    value={props.value} name={props.name}>
                        {options}
                    </select>
                </div>
            </div>
        </div>
    </div>
}
