import { MaterialType } from "../../Types/Types";
import { getMaterial } from "../../DAL/Api";
import { Dispatch } from "redux";

const SET_MATERIAL:"materialReducer/SET_MATERIAL"="materialReducer/SET_MATERIAL"


let initialState={
  id:0 as number,
  title:"" as string,
  text:"" as string,
  imgMaterial:"" as string
}

type InitialStateType=typeof initialState
type ActionType=SetMaterialType

export const materialReducer=(state=initialState,action:ActionType):InitialStateType=>{
  switch (action.type) {
    case SET_MATERIAL:
      return{
        ...action.material
      }
    default:
      return state
  }
}

type SetMaterialType={
  type:typeof SET_MATERIAL
  material:MaterialType
}
const setMaterial=(material:MaterialType):SetMaterialType=>{
  return{
    type:SET_MATERIAL,
    material
  }
}
export const setMaterialThunk=(id:number)=>async (dispatch:Dispatch)=>{
  let response=await getMaterial(id)

  dispatch(setMaterial(response.data))
}
