import { applyMiddleware, combineReducers, createStore } from "redux";
import thunkMiddleware from "redux-thunk";
import { egeChoiceReducer } from "./Reducers/egeChoiceReducer";
import { egeResultReducer } from "./Reducers/egeResultReducer";
import { egeVariantReducer } from "./Reducers/egeVariantReducer";
import { preResultReducer } from "./Reducers/preResultReducer";
import { singleQuestionReducer } from "./Reducers/singleQuestionReducer";
import { materialReducer } from "./Reducers/materialReducer";
import { headerReducer } from "./Reducers/headerReducer";


const rootReducer=combineReducers({
    mainPage:egeChoiceReducer,
    resultPage:egeResultReducer,
    variantPage:egeVariantReducer,
    preResultPage:preResultReducer,
    singleQuestionPage:singleQuestionReducer,
    material:materialReducer,
    header:headerReducer
})
type RootReducerType=typeof rootReducer
export type AppStateType=ReturnType<RootReducerType>

export const store=createStore(rootReducer,applyMiddleware(thunkMiddleware))
