
import { Dispatch } from "redux"
import { getNumbers, getVariants } from "../../DAL/Api"
import { NumberType, VariantType } from "../../Types/Types"

const SET_NUMBERS:"egeChoiceReducer/SET_NUMBER"="egeChoiceReducer/SET_NUMBER"
const SET_PAGE_VARIANTS:"egeChoiceReducer/SET_VARIANTS"="egeChoiceReducer/SET_VARIANTS"

const initialState={
    numbers:[] as NumberType[],
    variants:[] as VariantType[],
    pageVariant:1 as number,
    totalCount:0 as number
}

type InitialStateType = typeof initialState
type ActionType = SetNumbersType | SetVariantsType

export const egeChoiceReducer=(state=initialState,action:ActionType):InitialStateType=>{
    switch(action.type){
        case SET_NUMBERS:
            return{...state,numbers:[...action.numbers]}
        case SET_PAGE_VARIANTS:
            return{...state,variants:[...action.variants],
                pageVariant:action.page,
                totalCount:action.totalCount
            }
        default:
            return state
    }
}

type SetVariantsType={
    type:typeof SET_PAGE_VARIANTS
    variants:VariantType[]
    page:number
    totalCount:number
}
const setVariants=(variants:VariantType[],page:number,totalCount:number):SetVariantsType=>{
    return{
        type:SET_PAGE_VARIANTS,
        variants,page,totalCount
    }
}
export const setVariantsThunk=(page:number,item:number)=>async (dispatch:Dispatch)=>{
    let response = await getVariants(page,item)
    
    dispatch(setVariants(response.data.results,page,response.data.count))
}

type SetNumbersType={
    type:typeof SET_NUMBERS
    numbers:NumberType[]
}
const setNumbers=(numbers:NumberType[]):SetNumbersType=>{
    return{
        type:SET_NUMBERS,
        numbers
    }
}
export const setNumbersThunk=(item:number)=>async (dispatch:Dispatch)=>{
    let response = await getNumbers(item)

    dispatch(setNumbers(response.data))
}




