import { useEffect } from "react"
import { useDispatch, useSelector } from "react-redux"
import { useHistory } from "react-router"
import { QuestionTypeA, QuestionTypeB, setResult } from "../../../BLL/Reducers/egeResultReducer"
import { maxResultSelector, questionsSelector, resultSelector, } from "../../../BLL/Selectors/egeResultSelector"
import { BarResult } from "./BarResult"



type PropsType = {}
export const ResultPage: React.FC<PropsType> = (props) => {

    let results = useSelector(questionsSelector)
    let result = useSelector(resultSelector)
    let maxResult = useSelector(maxResultSelector)
    const dispatch = useDispatch()
    const history=useHistory()

    useEffect(() => {
        return (() => {
            //To zero all results
            dispatch(setResult({ A: [], B: [] }, true))
        })
    }, [])

    let questionsA=results.A.map(a=><BarResult  {...a}/>)
    let questionsB=results.B.map(b=><BarResult {...b}/>)

    const onClick=()=>{
        history.push({
            pathname:'/'
        })
    }

    return <div>
        <h3 className="center">
            Результаты
        </h3>
        <div className="row mx-2">
            {questionsA.length >0 &&
            <div className={`mt-1 ${questionsB.length>0 ? 'col-md-6' : 'col-md-12'}`}>
                <h5 className="center">Первая часть</h5>
                <table className="table border-success table-hovered">
                    <thead>
                        <th className="center yCenter">
                            №
                        </th>
                        <th className="center yCenter">
                            #
                        </th>
                        <th className="center yCenter">
                            Ваш отвтет
                        </th>
                        <th className="center yCenter">
                            Правильный отвтет
                        </th>
                    </thead>
                    <tbody>
                        {questionsA}
                    </tbody>
                </table>
            </div>}
            {questionsB.length >0 &&
            <div className={`mt-1 ${questionsA.length>0 ? 'col-md-6' : 'col-md-12'}`}>
                <h5 className="center">Вторая часть</h5>
                <table className="table border-success">
                    <thead>
                        <th className="center yCenter">
                            №
                        </th>
                        <th className="center yCenter">
                            #
                        </th>
                        <th className="center yCenter">
                            Ваш балл
                        </th>
                        <th className="center yCenter">
                            Максимальный балл
                        </th>
                    </thead>
                    <tbody>
                        {questionsB}
                    </tbody>
                </table>
            </div>}
        </div>
        <div className="center row mt-1">
            <div className="col-md-3 mySuccess text-white px-1 rounded">
                Задание решено верно
            </div>
            <div className="col-md-3 myWarning text-white px-1 rounded">
                Задание частично решено
            </div>
            <div className="col-md-3 myDanger text-white px-1 rounded">
                Задание решено неверно
            </div>
            <div className="col-md-3 px-1 rounded" style={{border:'1px silver solid'}}>
                Задание было пропущено
            </div>
        </div>
        <div className="text-center mt-1">
            Вы сделали работу на {Math.round((result/maxResult)*100)}%
            правильно , то есть набрали {result} {result !==1 ? "первичных" :"первичный"} баллов ,
            всего можно было набрать {maxResult}.
        </div>
        <div className="mt-1 center">
            <button className="btn btn-success"
            onClick={onClick}
            style={{fontSize:"1.2em"}}>
                На главную
            </button>
        </div>
    </div>
}
