type PropsType={

}
export const PreResultPage:React.FC<PropsType>=(props)=>{
    
    let questions=useSelector(questionsPreResultSelector)

    return<div>
        <h3>Вторая часть</h3>
        <div>
            <PreResultForm questions={questions} />            
        </div>
    </div>
}