import { Route, Switch } from "react-router"
import { EgeChoice } from "./EgeChoice/EgeChoice";
import { ResultPage } from "./EgeResult/ResultPage";
import { EgeVariantPage } from "./EgeVariant/EgeVariantPage";
import { PreResultPage } from "./preResult/PreResultPage";
import { SingleQuestion } from "./SingleQuestion/SingleQuestion";
import { Material } from "./Material/Material";


export const Article=()=>{
    return<div className="container mt-3 Article p-4">
        <Switch>
            <Route path="/main" render={()=><EgeChoice/>} />
            <Route path="/train" render={()=><EgeVariantPage/>}/>
            <Route path="/variant" render={()=><EgeVariantPage/>}/>
            <Route path="/preResult" render={()=><PreResultPage/>} />
            <Route path="/result" render={()=><ResultPage/>} />
            <Route path="/solveQuestion" render={()=><SingleQuestion/>}/>
            <Route path="/material" render={()=><Material/>} />
            <Route path="*" render={()=><EgeChoice/>} />
        </Switch>
    </div>
}
