import { AppStateType } from "../store";


export const numbersSelector=(state:AppStateType)=>{
    return state.mainPage.numbers
}
export const pageNumberSelector=(state:AppStateType)=>{
    return state.mainPage.pageVariant
}
export const variantsSelector=(state:AppStateType)=>{
    return state.mainPage.variants
}
export const totalCountSelector=(state:AppStateType)=>{
    return state.mainPage.totalCount
}
