import { Dispatch } from "redux"
import { getQuestion } from "../../DAL/Api"
import { BenchmarkType, CategoryType, NumberType, QuestionType } from "../../Types/Types"

const SET_QUESTION:"singleQuestionReducer/SET_QUESTION"="singleQuestionReducer/SET_QUESTION"

const initialState={
    id:0 as number,
    number:{
        id:0,
        categorys:[] as CategoryType[],
        title:"" ,
        number:0,
        points:0,
        benchmarks:[] as BenchmarkType[]
    } as NumberType,
    categorys:{
        id:0,
        title:""
    } as CategoryType,
    difficult:0 as number,
    introduction:"" as string,
    firstAsk:"" as string,
    secondAsk:"" as string,
    thirdAsk:"" as string,
    imgForAsk:"" as string,

    imgForAns:"" as string,
    fistInstruction:"" as string,
    secondInstuction:"" as string,
    thirdIntroduction:"" as string,

    firstAnswer:"" as string,
    secondAnwer:"" as string,
    thirdAnswer:"" as string,

    addition:"" as string,
    source:"" as string,
    source_link:"" as string   
}

type InitialStateType=typeof initialState
type ActionType=SetQuestionType

export const singleQuestionReducer=(state=initialState,action:ActionType):InitialStateType=>{
    switch(action.type){
        case SET_QUESTION:
            return{
                ...state,...action.question
            }
        default:
            return state
    }
}

type SetQuestionType={
    type:typeof SET_QUESTION
    question:QuestionType
}
export const setQuestion=(question:QuestionType):SetQuestionType=>{
    return{
        type:SET_QUESTION,
        question
    }
}
export const setQuestionThunk=(id:number)=>async (dispatch:Dispatch)=>{
    let response=await getQuestion(id)

    dispatch(setQuestion(response.data))
}